.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.nav {
  align-items: center;
  height: 100%;
  margin-left: 0;
  display: flex;
}

.nav__list {
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__link {
  white-space: nowrap;
  color: #060b35;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14.06px;
  text-decoration: none;
  display: flex;
}

.header__logo {
  align-items: center;
  display: flex;
}

.logo__img {
  width: 40px;
  height: 40px;
  display: inline-block;
}

a.is-active.nav__link {
  margin: 0;
}

.nav-list:first-child {
  margin-left: 0;
}

.nav-hover:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.nav-link:hover {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

body {
  width: 1200px;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
}

.card__photo {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__name {
  color: #060b35;
  text-align: left;
  margin-top: 0;
  margin-bottom: 4px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  text-align: left;
  color: #616070;
  margin-top: 0;
  margin-bottom: 16px;
  line-height: 14px;
}

.card__price {
  text-align: left;
  color: #616070;
  justify-content: space-between;
  margin-top: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  display: flex;
}

.card__price-value {
  text-align: right;
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__price-header {
  text-align: left;
  color: #616070;
  font-size: 12px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  height: 16px;
  margin-top: 0;
  margin-bottom: 24px;
  display: flex;
}

.card__review-count {
  text-align: right;
  color: #060b35;
}

.card__btn {
  color: #fff;
  text-transform: uppercase;
  box-sizing: border-box;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__btn:hover {
  color: #00acdc;
  background: #fff;
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 :first-child, .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(n+1) {
  background-image: url("star-active.edda4c43.svg");
}

.main {
  grid-template-columns: repeat(4, 1fr);
  justify-content: center;
  gap: 46px 48px;
  width: 944px;
  height: 862px;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

@media screen and (width <= 1024px) {
  .nav-link:hover {
    color: #00acdc;
